@import (less) 'color.less';

.sfondo-div {
  width: 100%;
  background-color: @green;
}

.u2056_div {
    border-width:0em;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    bottom:0px;
    min-width:960px;
    background:inherit;
    background-color:@green;
    border:none;
    border-radius:0px;
    -moz-box-shadow:none;
    -webkit-box-shadow:none;
    box-shadow:none;
    font-size:1.5em;
    color:@white;
  }
  .title-button{
      padding-top:  20px;
      padding-bottom:  25px;
  }
  .col-width {
    padding-left: 4.64%;
    padding-right: 4.64%;
    width: 33.33333333%;
    float: left;
  }
  .no-margin{
      margin-left: 0px;
      margin-right: 0px;
  }

  .row-button-line  {
      padding-bottom:20px;
  }

  .btn-bottom{
    border-radius: 2px !important;
    border-style: solid;
    border-width: 1px;
    width: 250px;
    height: 50px;
    text-align: center;
    background-color:@green;
    font : 'Arial Bold';
    font-size: 0.75em;
    color:  @white;
    &:hover {
        background-color: @darkest_gray;
        color: @white;
        border-color: @darkest_gray;
    }
  }

  .btn-boottom-container {
    display: -webkit-box;      /* OLD - iOS 6-, Safari 3.1-6 */
    display: -moz-box;         /* OLD - Firefox 19- (buggy but mostly works) */
    display: -ms-flexbox;      /* TWEENER - IE 10 */
    display: -webkit-flex;     /* NEW - Chrome */
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
  .btn-boottom-icon {
    -webkit-box-flex:  0 0 42px;      /* OLD - iOS 6-, Safari 3.1-6 */
    -moz-box-flex: 0 0 42px;         /* OLD - Firefox 19- */
    -webkit-flex: 0 0 42px;          /* Chrome */
    -ms-flex:  0 0 42px;              /* IE 10 */
    flex: 0 0 42px;
    font-size: 32px;
    margin-top: 4px;
  }
  .btn-boottom-label {
    padding-left: 10px;
    font : 'Arial Bold';
    font-size: 0.75em;
    color: @white;
    max-width: 12.33em;
    overflow: hidden;
    text-overflow: clip;
  }
